:root {
  --clr-prp: #4902fe;
  --clr-red: #fe7e7f;
  --clr-ylw: #babd00;
  --clr-grn: #1bda34;
}

::-webkit-scrollbar {
  width: 0;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Jost', sans-serif;
  font-size: 1.1rem;
}
html {
  scroll-behavior: smooth;
}
body {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  min-height: 100vh;
  background: linear-gradient(
    50deg,
    rgb(114, 95, 151),
    rgb(255, 255, 255),
    rgb(114, 95, 151)
  );
  animation: animate_bg 2s ease 5.5s infinite;
}
@keyframes animate_bg {
  0%,
  100% {
    filter: saturate(1);
  }
  50% {
    filter: saturate(3);
  }
}
